import React, { Component } from 'react'
import { NavBar, Icon ,Carousel,Grid} from 'antd-mobile';
import img from "../../img/homelun1_05.png";
// import homeimg from "../../img/images/homelun1_02.jpg"
import homeimg1 from "../../img/images/2_08.png"
import { relative } from 'path';

const data = ["2_05.png","2_05.png","2_05.png"].map((_val, i) => ({
    icon:require(`../../img/images/${_val}`)
}));
export default class AppHome extends Component {
    render() {
        return (
            <div style={{backgroundColor:"#ddd"}}>
                <NavBar
                    style={{backgroundColor:'rgb(63,204,203)',color:'#fff'}}
                    // rightContent={[
                    //     <Icon key="0" type="search" style={{ marginRight: '16px' }} />
                    // ]}
                >住吧家居</NavBar>
                <Carousel
                            autoplay={true}
                            infinite
                            beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                            afterChange={index => console.log('slide to', index)}
                            >
                            {["homelun1_02.jpg","2_08.png"].map(val => (
                                <a
                                key={val}
                                // href="http://www.alipay.com"
                                style={{ display: 'inline-block', width: '100%'}}
                                >
                                <img
                                    src={require(`../../img/images/${val}`)}
                                    // src={homeimg}
                                    alt=""
                                    style={{ width: '100%', verticalAlign: 'top' }}
                                    onLoad={() => {
                                    // fire window resize event to change height
                                    window.dispatchEvent(new Event('resize'));
                                    this.setState({ imgHeight: 'auto' });
                                    }}
                                />
                                </a>
                            ))}
                    </Carousel>
                    <div style={{backgroundColor:'#ddd'}}>
                        {/* <div className="sub-title"></div> */}
                        <Grid data={data}
                            itemStyle={{ background: '#ddd' ,height:100,paddingLeft:15}}
                            columnNum={3}
                            hasLine={false}
                            renderItem={dataItem=> (
                                <div style={{backgroundColor:"#ddd",float:'left'}}>
                                    <img src={dataItem.icon}  alt="" style={{width:90}}/>
                                    {/* <div style={{ color: '#888', fontSize: '14px', marginTop: '12px' }}>
                                        <span>I am title..</span>
                                    </div> */}
                                </div>
                            )}
                    />
                    </div>
                    <div style={{width:'100%',height:30,backgroundColor:'#fff',marginTop:15,paddingTop:5}}>
                        {/* <div style={{width:8,height:20,backgroundColor:'rgb(63,204,203)'}}></div> */}
                        <p>热门推荐</p>
                    </div>
                    <div><img src={homeimg1} style={{width:'100%'}}/></div>
            </div>
        )
    }
}

