import React, { Component } from 'react';
import { NavBar, Tabs,Icon ,Carousel,Grid} from 'antd-mobile';
// import { StickyContainer, Sticky } from 'react-sticky';
import lingganimg from "../../img/images/limg_03.png"

const tabs = [
    { title:"推荐"  },
    { title:"冬季" },
    { title:"宜家" },
    { title:"小清新"  },
    { title:"小户型" },
    { title:"个性色彩" },
];
const data = ["limg_03.png","limg_03.png","limg_03.png","limg_03.png","limg_03.png","limg_03.png","limg_03.png","limg_03.png","limg_03.png"].map((_val, i) => ({
    icon: require(`../../img/images/${_val}`),
}));
export default class Linggan extends Component {
    render() {
        return (
            <div>
                <NavBar
                    style={{backgroundColor:'rgb(63,204,203)',color:'#fff'}}
                    rightContent={[
                        <Icon key="0" type="search" style={{ marginRight: '30px' }} />
                    ]}
                >灵感</NavBar> 
                <div>
                    <Tabs tabs={tabs}
                    initialPage={'t2'}
                    // renderTabBar={renderTabBar}
                    >
                        <div style={{ backgroundColor: 'rgb(238,238,238)' }}>
                            <div style={{ backgroundColor: 'rgb(238,238,238)' }}>
                                <div className="sub-title"></div>
                                <Grid data={data}
                                itemStyle={{ background: '#ddd',height:160}}
                                columnNum={2}
                                renderItem={dataItem=> (
                                <div style={{ padding: '5px' ,backgroundColor:"#ddd"}}>
                                    <img src={dataItem.icon}  alt="" style={{width:120}}/>
                                </div>
                            )}
                    />
                    </div>
        </div>
        <div style={{backgroundColor: '#fff' }}>
            
        </div>
        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '250px', backgroundColor: '#fff' }}>
          Content of third tab
        </div>
      </Tabs>

  </div>
  {/* <div>
                        <div className="sub-title"></div>
                        <Grid data={data}
                            columnNum={3}
                            renderItem={dataItem=> (
                                <div style={{ padding: '5px' }}>
                                    <img src={dataItem.icon}  alt="" style={{width:50}}/>
                                </div>
                            )}
                    />
                    </div> */}
                







            </div>
        )
    }
}
