import React, { Component } from 'react';
import { NavBar, Icon ,Carousel,Grid,SearchBar} from 'antd-mobile';


const data = ["store_03.png","store_03.png","store_03.png","store_03.png","store_03.png","store_03.png","store_03.png","store_03.png","store_03.png","store_03.png"].map((_val, i) => ({
    icon:require(`../../img/images/${_val}`),
    text:"桌"
}));
const data1= ["store1_07.png","store1_07.png","store1_07.png","store1_07.png","store1_07.png","store1_07.png","store1_07.png","store1_07.png","store1_07.png","store1_07.png",].map((_val, i) => ({
    icon:require(`../../img/images/${_val}`),
    text:"顺顺工艺欧式风格茶杯"
}));

export default class AppStore extends Component {
    render() {
        return (
            <div>
               <NavBar
                    style={{backgroundColor:'rgb(63,204,203)',color:'#fff'}}
                    // rightContent={[
                    //     <Icon key="0" type="search" style={{ marginRight: '16px' }} />
                    // ]}
                    icon={<i className='iconfont icon-gouwuche' style={{position:"relative",left:230}}></i>
                    }
                >商城</NavBar> 
                <Carousel
                            autoplay={true}
                            infinite
                            beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                            afterChange={index => console.log('slide to', index)}
                            >
                            {["homelun1_02.jpg","2_08.png","homelun1_02.jpg","2_08.png"].map(val => (
                                <a
                                key={val}
                                // href="http://www.alipay.com"
                                style={{ display: 'inline-block', width: '100%'}}
                                >
                                  
                                <img
                                    src={require(`../../img/images/${val}`)}
                                    alt=""
                                    style={{ width: '100%', verticalAlign: 'top'}}
                                    onLoad={() => {
                                    // fire window resize event to change height
                                    window.dispatchEvent(new Event('resize'));
                                    this.setState({ imgHeight: 'auto' });
                                    }}
                                />
                                </a>
                            ))}
                    </Carousel>
                    <div style={{position:"relative",top:-200,left:50}}>
                        <input text='text' placeholder="请输入关键字搜索" style={{width:250,height:25, borderRadius:8,backgroundColor:'#ddd',paddingLeft:20}}/>  
                        <i style={{position:"relative",left:-250}} className='iconfont icon-sousuo'></i>
                    </div>
                    {/* <SearchBar style={{position:relative,top:-150}} placeholder="Search" maxLength={8} /> */}
                    <div style={{marginTop:-30}} >
                        <div className="sub-title"></div>
                        <Grid data={data}
                            itemStyle={{ background: '#fff',height:60}}
                            columnNum={5}
                            hasLine={false}
                            renderItem={dataItem=> (
                                <div style={{ padding: '0px'}}>
                                    <img src={dataItem.icon}  alt="" style={{width:30}}/>
                                    <div style={{ color: '#888', fontSize: '10px' }}>
                                        <span>{dataItem.text}</span>
                                    </div>
                                </div>
                            )}
                    />
                    </div>  
                    <div syle={{backgroundColor:"red"}}>            
                        <div>
                            <div className="sub-title"></div>
                            <Grid data={data1}
                                itemStyle={{ background: '#ddd',height:150}} 
                                columnNum={2}
                                renderItem={dataItem=> (
                                    <div style={{ padding: '5px'}}>
                                        <img src={dataItem.icon}  alt="" style={{width:130}}/>
                                        <div>
                                            <p style={{ color: '#888', fontSize: '10px' ,marginLeft:-30,marginTop:5}}>{dataItem.text}</p>
                                            <p style={{ color: '#888', fontSize: '10px',marginTop:-10,marginLeft:-105}}>￥23.4</p>
                                        </div>
                                    </div>
                                )}
                            />
                        </div>  
                    </div>
                                

            </div>
        )
    }
}
